import Vue from 'vue'
import '../../dist/template-1.css'
import SimpleCrop from '../../index.vue';

new Vue({
  el: '#app',
  data: {
    cropParams: {
      title: "",
      src: "../../img/test2.jpg",
      size: {
        width: 1000,
        height: 600
      },
      cropSizePercent: 0.65,
      scaleSlider: true,
      maxScale: 3,
      borderWidth: 1,
      funcBtns: ["close", "crop", "upload"],
      borderColor: "#fff",
      coverColor: "rgba(0,0,0,.5)",
      startAngle: -360,
      endAngle: 360,
    }
  },
  components: {
    'simple-crop': SimpleCrop
  },
  methods: {
    //
    updateComponent() {
      this.cropParams = JSON.parse(JSON.stringify(this.cropParams)); //
      this.cropParams.borderColor = '#0BFF00'; //
      this.cropParams.cropSizePercent = 0.5;
      this.cropParams.size = {
        width: 600,
        height: 600
      };
    },

    //
    setCropImage() {
      this.cropParams = JSON.parse(JSON.stringify(this.cropParams)); //
      this.cropParams.src = "../../img/test1.jpg";
    },

    //
    closeCallback() {
      console.log('Simple Crop ');
    },

    //
    cropCallback($resultCanvas) {
      $resultCanvas.style.marginRight = "10px";
      $resultCanvas.style.width = "50%";
      document.body.appendChild($resultCanvas);
    },

    //
    uploadCallback(src) {
      console.log('' + src);
    }
  }
});